header .navbar {
  align-items: baseline;
  justify-content: space-between;
  margin-left: -15px;
  margin-right: -15px;
  padding: 0 15px;
}

header .navbar .navbar-brand,
header .navbar .nav-item,
header .navbar .dropdown-divider,
header .navbar .navbar-toggler {
  margin-left: 15px;
  margin-right: 15px;
}

header .navbar .navbar-toggler {
  border-radius: 0;
  padding: 0;
  position: fixed;
  right: 0;
  top: 15px;
}

header .navbar .navbar-toggler i {
  color: #fa58a6;
}

header .navbar .nav-item i {
  margin-right: 10px;
}

header .navbar .nav-item i:last-child {
  font-size: .8rem;
  margin-left: 5px;
  margin-right: 0;
  padding: 10px 0;
}

header .navbar .nav-item section {
  padding: 15px 0;
}

header .navbar .nav-item section div {
  padding: 2px;
}

header .navbar .nav-item section .dropdown-divider {
  margin: 2px 0 0;
}

.app-menu {
  flex: 3 0 auto;
  justify-content: flex-end;
}

.app-menu .navbar-nav {
  align-items: flex-end;
}

.app-menu .nav-item .nav-link {
  align-items: baseline;
  display: flex;
  flex-flow: row nowrap;
  font-family: "Fenomen Sans Bold";
  font-size: 1.2rem;
  justify-content: flex-end;
  line-height: 2rem;
  padding: 2px 10px;
}

.app-menu .collapse.show div:hover {
  background-color: #50f;
}

