body {
  background: #fff;
  background-size: 130px;
  color: #000;
}

a {
  color: #000;
  text-decoration: none;
}

.app-menu .nav-item .active.nav-link,
.page-nav .nav-item .active.nav-link {
  background-position: 0% 85%;
  background-repeat: repeat-x;
  background-size: auto 5px;
  background: #ff01fb;
}

.app-menu .nav-item .nav-link:not(.active):hover,
.page-nav .nav-item .nav-link:not(.active):hover {
  background-position: bottom;
  background-repeat: repeat-x;
  background-size: auto 5px;
  padding-bottom: 0;
  text-decoration: none;
}

.error-list {
  padding: 15px;
  margin: 0;
}

.error-list li {
  border: 0;
  color: #000;
  list-style: circle inside;
  margin-top: 5px;
  font-size: .875rem;
  color: #555555;
}

.error-header {
  color: black;
}

.alert,
#error_explanation {
  background: #fff;
  border: 2px solid #faff00;
  border-radius: 0;
  color: #555555;
  margin: 20px auto;
  padding: 10px;
}

.alert-warning,
#error_explanation {
  background: rgba(2, 169, 234, 0.05);
}

.debug_dump {
  background-color: rgba(255, 255, 255, 0.7);
  margin: 20px auto;
  padding: 20px;
}

.field_with_errors label {
  font-family: "Fenomen Sans Bold Italic";
}

