.pagination {
  text-align: center;
  padding: 1em;
  cursor: default;
}

.pagination a,
.pagination span {
  padding: 0 0.3em;
}

.pagination .disabled {
  color: #aaaaaa;
}

.pagination .current {
  font-style: normal;
  font-weight: bold;
  background-color: #02a9ea;
  display: inline-block;
  width: 1.4em;
  height: 1.4em;
  line-height: 1.5;
  -moz-border-radius: 1em;
  -webkit-border-radius: 1em;
  border-radius: 1em;
}

.pagination a {
  text-decoration: none;
  color: black;
}

.pagination a:hover,
.pagination a:focus {
  text-decoration: underline;
}




